<template>
  <div class="entMain">
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="16" :offset="4">
          <div class="content-left">
            <!-- <div class="title"> -->
            <el-menu
              :default-active="activeIndex"
              class="el-menu-demo"
              mode="horizontal"
              @select="handleSelect"
            >
              <el-menu-item index="1">主动投递 ({{sentJob.length}})</el-menu-item>
              <el-menu-item index="2">邀请面试({{recJob.length}})</el-menu-item>
            </el-menu>
            <!-- </div> -->
            <!-- <div class="title">
              <span>
                <b>主动投递</b>
                ({{sentJob.length}})
              </span>
            </div>-->
            <div v-show="activeIndex=='1'">
              <div v-if="sentJob.length==0">
                <p>暂无投递</p>
              </div>

              <el-table v-else :data="tempList" stripe style="width: 100%" size="small">
                <el-table-column prop="name" label="姓名" width="80"></el-table-column>
                <el-table-column prop="jobName" label="投递职位" width="150"></el-table-column>
                <el-table-column prop="wantJobName" label="期望职位"></el-table-column>
                <el-table-column prop="wantJobPay" label="期望薪资"></el-table-column>
                <el-table-column prop="wantJobAdd" label="期望地点"></el-table-column>
                <el-table-column prop="sendTime" label="日期" sortable width="150"></el-table-column>
                <el-table-column label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button
                      @click="funClose(scope.row)"
                      type="text"
                      size="small"
                      style="color:#f56c6c;margin-left:10px"
                    >拒绝此人</el-button>
                    <!-- <router-link
                    :to="{name: 'singlepsn', query:{resumeid: scope.row.resumeid,psnid:scope.row.psnid}}"
                    target="_blank"
                    >-->
                    <el-button type="text" size="small" @click="funShow(scope.row)">查看简历</el-button>
                    <!-- </router-link> -->
                    <el-button
                      @click="funPass(scope.row)"
                      type="text"
                      size="small"
                      style="color:#67c23a"
                    >邀请面试</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                layout="prev, pager, next"
                :total="sentJob.length"
                style="text-align:center;"
                class="page"
                @size-change="funSizeChange"
                @current-change="funCurrentChange"
                :current-page="currentPage"
                :page-size="pageSize"
              ></el-pagination>
            </div>
            <div v-show="activeIndex=='2'">
              <div v-if="recJob.length==0">
                <p>暂无邀请</p>
              </div>
              <el-table v-else :data="tempListForRec" stripe style="width: 100%" size="small">
                <el-table-column prop="name" label="姓名" width="80"></el-table-column>
                <el-table-column prop="jobName" label="投递职位" width="110"></el-table-column>
                <el-table-column prop="wantJobName" label="期望职位" width="110"></el-table-column>
                <el-table-column prop="wantJobPay" label="期望薪资" width="80"></el-table-column>
                <el-table-column prop="wantJobAdd" label="期望地点" width="80"></el-table-column>
                <el-table-column
                  prop="status"
                  label="状态"
                  width="80"
                  :filters="[{ text: '面试邀请', value: '面试邀请' }, { text: '个人已同意', value: '个人已同意' }]"
                  :filter-method="filterTag"
                  filter-placement="bottom-end"
                >
                  <template slot-scope="scope">
                    <span
                      :type="scope.row.status === '面试邀请'"
                      disable-transitions
                    >{{scope.row.status}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="sendTime" label="日期" sortable width="100"></el-table-column>
                <el-table-column label="手机" width="100">
                  <template slot-scope="scope">
                    <span v-if="scope.row.status=='个人已同意'">{{scope.row.psntel}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="邮箱" width="100">
                  <template slot-scope="scope">
                    <span v-if="scope.row.status=='个人已同意'">{{scope.row.psnemail}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="80">
                  <template slot-scope="scope">
                    <el-button
                      @click="funDel(scope.row)"
                      type="text"
                      size="small"
                      v-if="scope.row.status=='个人已同意'"
                    >关闭</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                layout="prev, pager, next"
                :total="recJob.length"
                style="text-align:center;"
                class="page"
                @size-change="funSizeChange1"
                @current-change="funCurrentChange1"
                :current-page="currentPage1"
                :page-size="pageSize1"
              ></el-pagination>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <!-- <hr> -->
    <!-- <hr> -->
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "entMain",
  data() {
    return {
      activeIndex: "1",
      sentJob: [],
      recJob: [],
      entJobInfo: [],
      currentPage: 1,
      pageSize: 10,
      currentPage1: 1,
      pageSize1: 10,
      tempList: [],
      tempListForRec: []
    };
  },
  created() {
    this.axios
      .post("getSentJob", {
        entid: Cookies.get("entid"),
        psnid: ""
      })
      .then(data => {
        if (data.data.status == "ok") {
          let arr = JSON.parse(data.data.msg);
          for (var i in arr) {
            if (arr[i].fields.status == "已送达") {
              this.sentJob.push(arr[i].fields);
            } else if (
              arr[i].fields.status == "面试邀请" ||
              arr[i].fields.status == "个人已同意"
            ) {
              this.recJob.push(arr[i].fields);
            }
          }
          this.tempList = this.sentJob.slice(0, 10);
          this.tempListForRec = this.recJob.slice(0, 10);
        }
      });
  },
  methods: {
    /**表格筛选 */

    filterTag(value, row) {
      return row.status === value;
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    handleSelect(key) {
      this.activeIndex = key;
    },
    funDel(item) {
      this.recJob.splice(this.recJob.indexOf(item), 1);
      this.tempListForRec = this.recJob.slice(0, 10);
      this.axios
        .post("delSent", {
          msg: {
            jobid: item.jobid,
            resumeid: item.resumeid,
            entid: item.entid,
            psnid: item.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          }
        });
    },
    funClose(item) {
      this.sentJob.splice(this.sentJob.indexOf(item), 1);
      this.tempList = this.sentJob.slice(0, 10);
      this.axios
        .post("closeSent", {
          msg: {
            jobid: item.jobid,
            resumeid: item.resumeid,
            entid: item.entid,
            psnid: item.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          }
        });
    },
    funPass(item) {
      this.sentJob.splice(this.sentJob.indexOf(item), 1);
      this.tempList = this.sentJob.slice(0, 10);
      this.axios
        .post("passSent", {
          msg: {
            jobid: item.jobid,
            resumeid: item.resumeid,
            entid: item.entid,
            psnid: item.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          }
        });
    },
    funShow(item) {
      let routeData = this.$router.resolve({
        name: "singlepsn",
        query: { resumeid: item.resumeid, psnid: item.psnid, jobid: item.jobid }
      });
      window.open(routeData.href, "_blank");
    },
    funSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.funCurrentChange(this.currentPage);
    },
    funCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.currentChangePage(this.sentJob, currentPage);
    },
    //分页方法
    currentChangePage(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize;
      let to = currentPage * this.pageSize;
      this.tempList = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempList.push(list[from]);
        }
      }
    },
    funSizeChange1(pageSize) {
      this.pageSize1 = pageSize;
      this.funCurrentChange1(this.currentPage1);
    },
    funCurrentChange1(currentPage) {
      this.currentPage1 = currentPage;
      this.currentChangePage(this.recJob, currentPage);
    },
    currentChangePage1(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize1;
      let to = currentPage * this.pageSize1;
      this.tempListForRec = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempListForRec.push(list[from]);
        }
      }
    }
  }
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left .title {
  font-size: 16px;
  color: #606266;
}
.content-left .el-tag {
  font-size: 12px;
  color: #606266;
}

.el-tag + .el-tag {
  margin: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.content-left p {
  font-size: 16px;
  color: #606266;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
.comment {
  padding: 60px;
  /* background: #67c23a; */
  height: 36px;
  color: #303133;
  font-size: 18px;
  line-height: 36px;
}

.job-list {
  font-size: 14px;
  color: #606266;
}
.title {
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}
</style>
